import moment from 'moment'
import PlayCircleIcon from '@mui/icons-material/PlayCircleFilledOutlined'
import { Box, Tooltip } from '@mui/material'
import { convertYouTubeDuration, numberWithCommas } from '../../../../utils'

const renderThumbnail = (params, onVideoClick) => {
  const { thumbnails, title, videoId } = params.row ?? {}
  const thumbnail = thumbnails?.default ?? {}
  return (
    <a className="custom-table-thumbnail-wrapper" onClick={() => onVideoClick(videoId)}>
      <PlayCircleIcon fontSize="large" />
      <img src={thumbnail.url} width={thumbnail.width} height={thumbnail.height} alt={title} />
    </a>
  )
}

const renderTitleWithTooltip = (params) => {
  const { title } = params.row ?? {}
  return (
    <Tooltip title={title} placement="bottom-start">
      <Box sx={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>{title}</Box>
    </Tooltip>
  )
}

const formatDate = (params) => (params.value ? moment(params.value).format('YYYY/MM/DD HH:mm:ss') : '')

const formatNumberWithComma = (params) => (params.value ? numberWithCommas(params.value) : 0)

const formatDuration = (params) => (params.value ? convertYouTubeDuration(params.value) : '')

const columns = (onVideoClick) => [
  {
    field: 'thumbnail',
    headerName: '',
    minWidth: 150,
    sortable: false,
    headerAlign: 'center',
    align: 'center',
    renderCell: (params) => renderThumbnail(params, onVideoClick),
  },
  {
    field: 'title',
    headerName: '',
    flex: 1,
    minWidth: 150,
    sortable: false,
    headerAlign: 'center',
    renderCell: renderTitleWithTooltip,
  },
  {
    field: 'duration',
    headerName: '',
    minWidth: 100,
    sortable: false,
    align: 'center',
    headerAlign: 'center',
    valueFormatter: formatDuration,
  },
  {
    field: 'publishedAt',
    headerName: '',
    minWidth: 180,
    sortable: false,
    type: 'dateTime',
    align: 'center',
    headerAlign: 'center',
    valueFormatter: formatDate,
  },
  {
    field: 'viewCount',
    headerName: '',
    minWidth: 130,
    sortable: false,
    type: 'number',
    valueFormatter: formatNumberWithComma,
  },
  {
    field: 'likeCount',
    headerName: '',
    minWidth: 130,
    sortable: false,
    type: 'number',
    valueFormatter: formatNumberWithComma,
  },
  {
    field: 'commentCount',
    headerName: '',
    minWidth: 130,
    sortable: false,
    type: 'number',
    valueFormatter: formatNumberWithComma,
  },
]

export default columns
