import moment from 'moment'
import momentDurationFormatSetup from 'moment-duration-format'
// install duration format plugin
momentDurationFormatSetup(moment)

/**
 * @description 
 * @param {string | number} number 
 * @returns 
 */
export const numberWithCommas = (number) => {
  if (!number && number !== 0) return ''
  const x = number.toString()
  const parts = x.split('.')
  const reg = /\B(?=(\d{3})+(?!\d))/g
  parts[0] = parts[0].replace(reg, ',')
  return parts.join('.')
}

/**
 * YouTubes Data API returns durations in ISO_8601 format.
 * The durations prefixed with PT. P stands for period, T stands for time.
 * @description  Youtube API duration  h:mm:ss
 * @param {string} youtubeDuration
 * @returns h:mm:ss
 */
export const convertYouTubeDuration = (youtubeDuration) => moment.duration(youtubeDuration).format('h:mm:ss', { trim: false })

/**
 *  xx.xx
 * @param {number | string} number
 * @returns 
 */
export const shortenNum = (number) => {
  const n = Number(number)
  if (!n) return ''
  if (n < 10000) return String(n)
  return `${(Math.round((n / 10000) * 100)) / 100}`
}
